	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2012/7/the-reason-why-were-upset-about-sparrows-acquisition/index.md absPath of file >>> MarkdownRemark","html":"<p>I was going to write a post about Sparrow’s talent acquisition by Google, but Rian van der Merwe did it for me:</p>\n<blockquote>\n<p>I paid full price for every version of the Sparrow app I could find. I told everyone who would listen to buy it. I couldn’t have given them more money even if I wanted to. So, as a customer, what more could I have done to keep them running independently?   </p>\n<p><em>This</em> is the core of the disappointment that many of us feel with the Sparrow acquisition. It’s not about the $15 or less we spent on the apps. It’s not about the team’s well-deserved payout. It’s about the loss of faith in a philosophy that we thought was a sustainable way to ensure a healthy future for independent software development, where most innovation happens.</p>\n</blockquote>","frontmatter":{"title":"The reason why we’re upset about Sparrow’s acquisition","date":"July 21, 2012","passthroughUrl":"http://www.elezea.com/2012/07/sparrow-google-acquisition/"}}},"pathContext":{"slug":"/blog/2012/7/the-reason-why-were-upset-about-sparrows-acquisition/","previous":{"fields":{"slug":"/blog/2012/7/how-to-prepare-for-googles-acquisition-of-sparrow/"},"frontmatter":{"title":"How to prepare for Google's acquisition of Sparrow","passthroughUrl":"http://www.youtube.com/watch?v=aEbNZPIXaZ8","type":null}},"next":{"fields":{"slug":"/blog/2012/7/google-nexus-7-ad/"},"frontmatter":{"title":"Google Nexus 7 Ad","passthroughUrl":"http://www.youtube.com/watch?v=qqiSE-ukmgc","type":null}}}}

